import React from 'react';
import {observer} from "mobx-react-lite";
import {CustomDiv} from "../../../../core/components/UI/CustomDiv/CustomDiv";
import AppData from "../../../AppData";
import './PageContact.scss'

export default observer(() => {
        return (
            <CustomDiv className='h-full'>
                {
                    AppData.contacts.map((itemContact) =>
                        <div>
                            <div className='page-contact__title'>{itemContact.title}</div>
                            <div className='page-contact__card'>
                                {
                                    itemContact.body.map((itemBody) =>
                                        <a href={itemBody.link}  target={"_blank"} className='page-contact__item btn btn-ghost no-animation' rel="noreferrer">
                                            <div className="avatar mr-4">
                                                <div className="w-8 mask mask-squircle">
                                                    <img src={itemBody.icon}/>
                                                </div>
                                            </div>
                                            <span className='page-contact__text'>
                                                {itemBody.login}
                                            </span>
                                        </a>
                                    )
                                }
                            </div>
                        </div>
                    )
                }
            </CustomDiv>
        );
    }
)
