import React from 'react';
import mainStateStore from "../../../lib/store/main-state-store";
import { observer } from "mobx-react-lite";
import { DataProject } from "../PageProject/Data/DataProject";
import './PageInfoProject.scss'
import { CustomDiv } from "../../../../core/components/UI/CustomDiv/CustomDiv";
import { BodyInfoProject } from "./BodyInfoProject/BodyInfoProject";

export default observer(() => {
    const pathname = mainStateStore.pathname.split("/")
    const filterData = DataProject.filter((item) => (item.id === pathname[2]))

    return (
        <CustomDiv>
            {
                filterData.length !== 0 ?
                    filterData[0].projectEntities.map((itemProject, index) =>
                        <BodyInfoProject key={`page-info-project${index}`} itemProject={itemProject}
                            pathname={Number(pathname[3])} />
                    )
                    :
                    <div>
                        Такого нет
                    </div>
            }
        </CustomDiv>
    );
})

