import {makeAutoObservable} from 'mobx'
import {UsersType} from "../models/types/UsersType";
import services from "../services/services";


interface ISearchStateStore {
    listSearch: UsersType[]
    valueSearch: string
    exampleElement: string
    valueElement: number
    onFocus: boolean
}

class SearchStateStore implements ISearchStateStore {
    listSearch: UsersType[] = []
    valueSearch = ''
    exampleElement = ''
    valueElement = 0
    onFocus = false

    constructor() {
        makeAutoObservable(this)
    }

    setListSearch(val: UsersType[]) {
        this.listSearch = val
    }

    setValueSearch(val: string) {
        this.valueSearch = val
    }

    setExampleElement(val: string) {
        this.exampleElement = val
    }

    setValueElement(val: number) {
        this.valueElement = val
    }

    setOnFocus(val: boolean) {
        this.onFocus = val
    }

    posts(): void {
        services
            .getUsers()
            .then((res) => {
                this.setListSearch(res)
            })

    }
}

export default new SearchStateStore()
