import axios from 'axios'
import { UsersType} from "../models/types/UsersType";


export default {

    async getUsers(): Promise<UsersType[]> {

        let result: UsersType[]
        const params: string = 'users'
        const url = `https://jsonplaceholder.typicode.com/${params}`

        await axios
            .get(url)
            .then((res) => {
                result = res.data
            })
            .catch(() => {
                }
            )
            .then(() => {

            })

        // @ts-ignore
        return result
    },
}
