import { observer } from "mobx-react-lite";
import { useEffect, useState, useCallback } from "react";
import { Link, useLocation } from "react-router-dom";
import mainStateStore from "../../../../app/lib/store/main-state-store";
import routes from "../../../../app/lib/routes";
import { routesDesc } from "../../../../app/lib/routesDesc";
import './Navbar.scss';

interface Interface {
    children: React.ReactNode;
}

export default observer(({ children }: Interface) => {
    const [stateCheckbox, setStateCheckbox] = useState(false);
    const location = useLocation();

    useEffect(() => {
        mainStateStore.setPathname(location.pathname);
    }, [location.pathname]);

    const onChangeStateCheckbox = useCallback(() => {
        setStateCheckbox(prevState => !prevState);
    }, []);

    const links = routesDesc.map((rout, index) => {
        const isActive = location.pathname.includes(rout.url);
        return (
            <li key={`navbar-${rout.title}-${index}`}>
                <Link
                    onClick={onChangeStateCheckbox}
                    to={rout.url}
                    className={`${isActive ? 'custom__active' : 'custom__inactive'}`}>
                    <span className={`${isActive ? 'custom__active__icon' : 'custom__inactive__icon'}`}>{rout.icon}</span>
                    {rout.title}
                </Link>
            </li>
        );
    });

    return (
        <div className="drawer lg:drawer-open">
            <input id="my-drawer-2" type="checkbox" onClick={onChangeStateCheckbox} className="drawer-toggle"
                   checked={stateCheckbox}/>
            <div className="drawer-content">
                <div className="navbar lg:hidden sticky top-0 z-20">
                    <div className="navbar-start">
                    
                    </div>
                    <div className="navbar-end"/>
                </div>
                <div className='p-4 h-full'>
                    {children}
                </div>
            </div>
            <div className="drawer-side z-50">
                <label htmlFor="my-drawer-2" aria-label="close sidebar"
                       className="drawer-overlay"></label>
                <ul tabIndex={12} className="menu p-4 w-64 min-h-full text-base-content">
                    {links}
                </ul>
            </div>
        </div>
    );
});
