import React from 'react';
import IconStore from "../../../../../core/components/Icons/IconStore";

interface Interface {
    itemProject: {
        id: number,
        body: Array<{
            title: string,
            link?: string,
            component?: React.ReactNode,
            desc: string,
            img?: Array<{ link: string, title: string }>
        }>
    },
    pathname: number
}

export const BodyInfoProject = ({ itemProject, pathname }: Interface) => {
    if (itemProject.id !== pathname) return null;

    return (
        <>
            {itemProject.body.map(({ title, link, component, desc, img }, index) => (
                <div key={`body-info-${index}`}>
                    <div className='page-info-project__title'>
                        {title}
                        {link && (
                            <a href={link} target="_blank" title="Перейти на сайт" className='btn btn-ghost' rel="noreferrer">
                                {IconStore.arrowUpRightFromSquare}
                            </a>
                        )}
                    </div>
                    <div className='page-info-project__desc'>
                        {component && (
                            <div className='w-full h-full custom__iframe'>{component}</div>
                        )}
                        <div dangerouslySetInnerHTML={{ __html: desc }} />
                        {img?.map(({ link, title }, imgIndex) => (
                            <div key={`body-img-${index}-${imgIndex}`} className="tooltip tooltip-bottom" data-tip={title}>
                                <button className="btn btn-ghost">
                                    <div className="avatar">
                                        <div className="w-8 mask mask-squircle">
                                            <img src={link} alt={title} />
                                        </div>
                                    </div>
                                </button>
                            </div>
                        ))}
                    </div>
                </div>
            ))}
        </>
    );
};
