import React, {useCallback} from 'react';
import {observer} from "mobx-react-lite";
import searchStateStore from "../../../lib/store/search-state-store";
import {useLocalStorage} from 'usehooks-ts'
import {UsersType} from "../../../lib/models/types/UsersType";
import './PageSearch.scss'
import IconStore from "../../../../core/components/Icons/IconStore";

export default observer(() => {
        const [pastRequests, setPastRequests] = useLocalStorage<UsersType[]>('pastRequests', [])

        const {valueElement, exampleElement, valueSearch, listSearch, onFocus} = searchStateStore

        const {cachedData, filteredData} = valueSearch.length > 0 ?
            {
                cachedData: false,
                filteredData: listSearch.filter((val) => val.name.toLowerCase().startsWith(valueSearch.toLowerCase())).slice(0, 5)
            } : {
                cachedData: true,
                filteredData: pastRequests.filter((val) => val.name.toLowerCase().startsWith(valueSearch.toLowerCase())).slice(0, 5).reverse()
            }

        const onClickElement = (data: UsersType) => {
            searchStateStore.setExampleElement(data.name)
            if (!pastRequests.find((element) => element.id === data.id)) {
                setPastRequests([...pastRequests, data])
            }
            searchStateStore.setOnFocus(false)
            searchStateStore.setValueSearch('')
        }
        const SelectingElement = (event: React.KeyboardEvent<HTMLInputElement>) => {
            switch (event.key) {
                case 'ArrowUp':
                    searchStateStore.setValueElement(valueElement === -1 ? filteredData.length - 1 : valueElement - 1);
                    event.preventDefault()
                    break;
                case 'ArrowDown':
                    searchStateStore.setValueElement(valueElement > filteredData.length - 1 ? 0 : valueElement + 1);
                    event.preventDefault()
                    break;
                case 'Enter':
                    searchStateStore.setExampleElement(filteredData[valueElement].name);
                    if (!pastRequests.find((element: any) => element.id === filteredData[valueElement].id)) {
                        setPastRequests([...pastRequests, filteredData[valueElement]])
                    }
                    searchStateStore.setOnFocus(false)
                    searchStateStore.setValueSearch('');
                    break;
                case 'Escape':
                    searchStateStore.setValueSearch('');
                    break;
                default:
                    break;
            }
        }
        const deleteElement = (element: UsersType) => {
            const data = pastRequests
            delete data[data.indexOf(element)]
            const changeData = data.filter((item) => item !== null)
            setPastRequests(changeData)
        }
        const onchangeValueSearch = useCallback((e: any) => {
            searchStateStore.setValueSearch(e.target.value)

        }, [])
        return (
            <div className='p-4'>
                <div className='mb-4'>
                    Вы выбрали: <span className='font-bold'>{exampleElement}</span>
                </div>

                <div>

                    <input
                        autoFocus={true}
                        onFocus={() => searchStateStore.setOnFocus(true)}
                        onKeyDown={SelectingElement}
                        onChange={onchangeValueSearch}
                        value={valueSearch}
                        type="text"
                        required
                        placeholder="Поиск"
                        className="input input-bordered w-full custom-input"/>

                    <ul className='mt-4'>
                        {
                            onFocus &&
                            filteredData.map((data, index) => (
                                <li
                                    key={`page-search-${index}`}
                                    className={`flex justify-between items-center hover:bg-blue-500 hover:text-white ${valueElement === index && 'bg-amber-200'} rounded-xl`}
                                >
                                    <div
                                        className={`flex items-center p-4 cursor-pointer w-full`}
                                        onClick={() => onClickElement(data)}>
                                        <span
                                            className={` top__icon mr-2`}>{cachedData ? IconStore.clock : IconStore.search}</span>
                                        {data.name}
                                    </div>
                                    <div onClick={() => {
                                        cachedData ? deleteElement(data) : onClickElement(data)
                                    }} className='btn btn-ghost m-2 disabled'>
                                        {cachedData ? IconStore.xmark : IconStore.arrowUpRightFromSquare}
                                    </div>
                                </li>
                            ))
                        }
                    </ul>
                </div>
            </div>
        );
    }
)
