import {HashRouter, Redirect, Route, Switch} from 'react-router-dom'
import React from 'react'
import {observer} from 'mobx-react-lite'
import routes from "../../lib/routes";
import PageContacts from "../pages/PageContacts/PageContacts";
import Navbar from "../../../core/components/UI/Navbar/Navbar";
import PageSearch from "../pages/PageSearch/PageSearch";
import PageMyProject from "../pages/PageProject/PageProject";
import PageInfoProject from "../pages/PageInfoProject/PageInfoProject";
import PageTechnology from "../pages/PageTechnology/PageTechnology";


export default observer(() => {
    return (
        <HashRouter basename={'/'}>
            <Navbar>
                <Switch>
                    <Route exact path={routes.HOME} component={PageMyProject}/>
                    <Route exact path={routes.CONTACTS} component={PageContacts}/>
                    <Route exact path={routes.TECHNOLOGY} component={PageTechnology}/>
                    <Route exact path={routes.SEARCH} component={PageSearch}/>
                    <Route exact path={`${routes.HOME}/:id/:id`} component={PageInfoProject}/>
                    <Redirect from="*" to={routes.HOME}/>
                </Switch>
            </Navbar>
        </HashRouter>
    )
})

