import {observer} from "mobx-react-lite"
import React from "react";
import './PageProject.scss'
import {DataProject} from "./Data/DataProject";
import {Link} from "react-router-dom";
import routes from "../../../lib/routes";
import {CustomDiv} from "../../../../core/components/UI/CustomDiv/CustomDiv";

export default observer(() => {

    return (
        <CustomDiv>
            {
                DataProject.map((rout, index) => (
                        <div key={`page-project-${index}`} className='my-project__body'>
                            <div className='my-project__title'>
                                {rout.title}
                            </div>
                            <div className='my-project__cards'>
                                {
                                    rout.projectEntities.map((itemProject) => (
                                        <Link key={`my-project-${itemProject.title}-${index} `}
                                              to={`${routes.HOME}/${rout.id}/${itemProject.id}`}
                                              className='my-project__card'>
                                            {
                                                itemProject.icon !== undefined ?
                                                    <span
                                                        className={` custom__active__icon mr-4 my-project__circle`}>{itemProject.icon}</span>
                                                    :
                                                    <div className="avatar">
                                                        <div className="w-12 mr-4 mask mask-squircle">
                                                            <img src={itemProject.img} alt={itemProject.title}/>
                                                        </div>
                                                    </div>
                                            }

                                            {itemProject.title}
                                        </Link>
                                    ))
                                }
                            </div>
                        </div>

                    )
                )
            }
        </CustomDiv>
    )
})
